a::before {
  content: "";
  display: block;
  position: absolute;
  top: -7px;
  left: -3px;
  right: 0;
  height: 23px;
  background: url(data:image/png;base64,)
      no-repeat 0 0,
    url(data:image/png;base64,)
      no-repeat 50% 0,
    url(data:image/png;base64,)
      no-repeat 100% 0;
}

strong {
  color: #cc231e;
  /* color: rgb(250, 240, 227); */

  font-size: larger;
}

.buttons {
  display: grid;
  position: absolute;
  bottom: 1vh;
  left: auto;
  justify-content: center;
  width: 100%;
}

